import { BitBuilder, BitReader, Builder, Cell, Slice, beginCell } from '@ton/core'

import {
    AddressListTypeLike,
    AddressTypeLike,
    LoadableDict,
    MAX_CELL_BITS,
    NAME_WIDTH,
    TonObjects,
    baseBuildClass,
} from './index'
import { addressToBigInt } from '../utils'

export const isLoadableDict = <_TonObjects extends TonObjects>(value: unknown): value is LoadableDict<_TonObjects> => {
    return (
        value != null &&
        typeof value === 'object' &&
        'getDict' in value &&
        typeof value.getDict === 'function' &&
        'rawCell' in value &&
        value.rawCell instanceof Cell
    )
}

export const asciiStringToBigint = (target: string): bigint => {
    return BigInt(`0x${Buffer.from(target).toString('hex')}`)
}

export const bigintToAsciiString = (target: bigint): string => {
    return Buffer.from(target.toString(16), 'hex').toString('ascii')
}

export const getCellNameNumber = (cell: Cell): bigint => {
    const slice = cell.beginParse()
    const name = slice.loadUintBig(NAME_WIDTH)
    return name
}

export const getCellName = (cell: Cell): string => {
    const nameNumber = getCellNameNumber(cell)
    return bigintToAsciiString(nameNumber)
}

export const emptyCell = (): Cell => {
    return beginCell().asCell()
}

export const nullObject = (): Cell => {
    return emptyCell()
}

export const emptyMap = (): Cell => {
    return emptyCell()
}

export const emptyPOOO = (): Cell => {
    return baseBuildClass('POOO', {
        nextEmpty: 1,
        bitmap: ((): Cell => {
            const builder = new BitBuilder(MAX_CELL_BITS)
            builder.writeUint(0, 1023)

            const bitString = builder.build()
            const bitReader = new BitReader(bitString)
            const slice = new Slice(bitReader, [])

            return slice.asCell()
        })(),
    })
}

export const initBaseStorage = (owner: AddressTypeLike): Cell => {
    return baseBuildClass('BaseStorage', {
        owner,
        authenticated: false,
        initialized: false,
        initialStorage: emptyCell(),
    })
}

export function serializeAddressList(addresses: AddressListTypeLike): Cell {
    if (addresses instanceof Cell) {
        return addresses
    }

    const cellBuilders: Builder[] = [beginCell()]
    for (const address of addresses) {
        if (cellBuilders[cellBuilders.length - 1].availableBits < 256) {
            cellBuilders.push(beginCell())
        }
        cellBuilders[cellBuilders.length - 1].storeUint(addressToBigInt(address), 256)
    }
    let cell: Cell = cellBuilders[cellBuilders.length - 1].endCell()
    for (let i = cellBuilders.length - 2; i >= 0; i--) {
        cell = cellBuilders[i].storeRef(cell).endCell()
    }
    return cell
}

export function deserializeAddressList(cell: Cell): bigint[] {
    const addresses: bigint[] = []

    let currentCell: Cell | null = cell
    while (currentCell) {
        const slice = currentCell.beginParse()
        // Iterate through all the addresses in the current cell
        while (slice.remainingBits >= 256) {
            // Extract 256-bit address and add it to the addresses list
            addresses.push(slice.loadUintBig(256))
        }
        // Move to the next reference cell if available
        currentCell = slice.remainingRefs > 0 ? slice.loadRef() : null
    }

    return addresses
}
