import { BitBuilder, BitString, Builder, Cell, beginCell } from '@ton/core'

import { MAX_CELL_BITS } from './class'

export const hexToCells = (hex: string): Cell => {
    const bitString = new BitString(Buffer.from(hex.slice(2), 'hex'), 0, hex.slice(2).length * 4)

    const cells: Builder[] = []

    let offset = 0
    while (offset < bitString.length) {
        cells.push(beginCell())
        cells[cells.length - 1].storeBits(
            bitString.substring(offset, Math.min(bitString.length - offset, MAX_CELL_BITS))
        )
        offset += MAX_CELL_BITS
    }

    if (cells.length === 0) {
        return beginCell().asCell()
    }

    cells.reverse()

    return cells
        .slice(1)
        .reduce((children, parent) => parent.storeRef(children), cells[0])
        .asCell()
}

export const getTotalBits = (cell: Cell): number => {
    const parser = cell.beginParse()

    return parser.remainingBits + (parser.remainingRefs > 0 ? getTotalBits(parser.loadRef()) : 0)
}

export const getClosestByteAlignedBits = (bits: number): number => {
    return Math.ceil(bits / 8) * 8
}

export const cellsToHex = (cell: Cell): string => {
    let parser = cell.beginParse()

    const totalBits = getClosestByteAlignedBits(getTotalBits(cell))
    const bitBuilder = new BitBuilder(totalBits)

    do {
        const bitsString = parser.loadBits(parser.remainingBits)
        bitBuilder.writeBits(bitsString)
        if (parser.remainingRefs === 0) {
            break
        }
        parser = parser.loadRef().beginParse()
    } while (parser.remainingRefs >= 0)

    return `0x${bitBuilder.buffer().toString('hex')}`
}

export const calculateCellConsumption = (cell: Cell): { numberOfCells: number; bits: number } => {
    let parser = cell.beginParse()

    let numberOfCells = 1
    let bits = parser.remainingBits

    while (parser.remainingRefs > 0) {
        parser = parser.loadRef().beginParse()
        numberOfCells++
        bits += parser.remainingBits
    }

    return { numberOfCells, bits }
}

export const calculateCellConsumptionFromHex = (hex: string): { numberOfCells: number; bits: number } => {
    return calculateCellConsumption(hexToCells(hex))
}
