import { Cell } from '@ton/core'

import { NAME_WIDTH, bigintToAsciiString, getCellName } from './class'
import { tonObjects } from '../offchain-helpers'

//the true name of the class
type TonRealNameType = keyof typeof tonObjects
//the name as extracted from the cell
type TonCellNameType = (typeof tonObjects)[keyof typeof tonObjects]['name']

type TonRealNameByCellName = {
    [K in TonCellNameType]: TonRealNameType
}

export type Primitive = string | number | bigint | unknown[] | boolean | object

export const tonRealNameByCellName = Object.fromEntries(
    Object.entries(tonObjects)
        .filter(([_, v]) => {
            // Filter out empty objects
            return Object.keys(v).length > 1
        })
        .map(([k, v]) => [v.name, k])
) as TonRealNameByCellName

/**
 * Get the class name of a TON cell. The name encoded in the cell is
 * different from the desired "true" name, so the name is converted.
 * @param classCandidate
 * @returns the corrected name, as used by decodeClass(), or NULL if it can't be parsed.
 */
export const getName = (classCandidate: Cell): TonRealNameType | null => {
    try {
        //try to get the name from the first 10 bytes of the cell
        const cellName = classCandidate.asSlice().loadUintBig(NAME_WIDTH)
        const asciiCellName = bigintToAsciiString(cellName) as TonCellNameType
        //check if the name is legitimate, and then find the real name
        return tonRealNameByCellName[asciiCellName]
    } catch (e) {
        return null
    }
}

/**
 * Recursively traverses a TON cell, deciding how to decode each node based on
 * the class names stored in the cell.
 * @param cell the root cell to begin decoding from.
 * @param hashUndecodable if TRUE, cells that can't be decoded will be hashed. if FALSE, they will be returned unchanged.
 * This does not apply to primitive values.
 * @returns an object whose fields are each the fields of the decoded cell, and whose (cell) fields are themselves decoded.
 */
export const deepDecode = <
    Recurse extends { [key: string]: Recurse },
    ClassName extends 'POOO',
    DecodeClass extends (name: ClassName, cell: Cell) => { [key: string]: Cell | Primitive },
>(
    decodeClass: DecodeClass,
    cell: Cell | Primitive,
    hashUndecodable = false
): Recurse | Primitive => {
    if (!(cell instanceof Cell)) {
        //"cell" is a primitive, or otherwise can't be decoded as it is not a TON cell
        return cell
    }

    try {
        const decoded = decodeClass(getCellName(cell) as ClassName, cell)
        return Object.fromEntries(
            //cell was decoded successfully, recurse
            Object.entries(decoded).map(([k, v]) => [
                k,
                deepDecode<Recurse, ClassName, DecodeClass>(decodeClass, v, hashUndecodable),
            ])
        )
    } catch (e) {
        //could not decode cell, probably because it has no class name. either hash it, or return it
        return hashUndecodable ? cell.hash().toString('hex') : cell
    }
}

export const printDeepDecode = <
    Recurse extends { [key: string]: Recurse },
    ClassName extends 'POOO',
    DecodeClass extends (name: ClassName, cell: Cell) => { [key: string]: Cell | Primitive },
>(
    decodeClass: DecodeClass,
    cell: Cell | Primitive
): void => {
    console.log(
        JSON.stringify(
            deepDecode<Recurse, ClassName, DecodeClass>(decodeClass, cell, true),
            (_, v): unknown => (typeof v === 'bigint' ? v.toString() : (v as unknown)),
            2
        )
    )
}
