import { OPCODES } from '../constants'

const REVERSED_OPCODES: {
    [key: string]: string
} = Object.fromEntries(Object.entries(OPCODES).map(([key, value]) => [value.toString(), key])) as {
    [key: string]: string
}

// Function to get the operation name from the opcode
export function opcodeToName(opcode: bigint | number): string {
    return REVERSED_OPCODES[opcode.toString()] ?? 'UNKNOWN'
}
