// from https://github.com/LayerZero-Labs/offchain-monorepo/blob/main/packages/contracts/lz-ton-contracts/src/classes/index.ts
import { Address, Cell } from '@ton/core'

import {
    AddressListTypeLike,
    AddressTypeLike,
    BoolTypeLike,
    DictionaryTypeLike,
    EnhancedCell,
    Fields,
    LoadableDict,
    NumberTypeLike,
    emptyCell,
    emptyMap,
    generateBuildClass,
    generateDecodeClass,
    initBaseStorage,
    nullObject,
} from '../common'
import { tonResolvedObjects } from '../offchain-helpers'

export interface PathLike {
    srcEid: NumberTypeLike
    dstEid: NumberTypeLike
}
export type PathWayLike = PathLike &
    ({ sender: AddressTypeLike; receiver: AddressTypeLike } | { srcOApp: AddressTypeLike; dstOApp: AddressTypeLike })

type LZTonObjects = typeof tonResolvedObjects

export type ClassNames = keyof LZTonObjects
export type LZFields<
    ClassName extends ClassNames,
    ClassType extends Cell | EnhancedCell<LZTonObjects> = EnhancedCell<LZTonObjects>,
    NumberType extends NumberTypeLike = bigint,
    BoolType extends BoolTypeLike = boolean,
    AddressType extends AddressTypeLike = Address,
    AddressListType extends AddressListTypeLike = Address[],
    DictionaryType extends DictionaryTypeLike<unknown> = LoadableDict<LZTonObjects>,
> = Fields<LZTonObjects, ClassName, ClassType, NumberType, BoolType, AddressType, AddressListType, DictionaryType>

export const buildClass = generateBuildClass(tonResolvedObjects)
export const decodeClass = generateDecodeClass(tonResolvedObjects)

export const initBaseOApp = (args: {
    controllerAddress: AddressTypeLike
    srcEid: NumberTypeLike
    baseLzReceiveGas: NumberTypeLike
    endpointCode: Cell
    channelCode: Cell
}): Cell => {
    return buildClass('BaseOApp', {
        controllerAddress: args.controllerAddress,
        eid: args.srcEid,
        maxReceivedNonce: emptyMap(),
        baseLzReceiveGas: args.baseLzReceiveGas,
        peers: emptyMap(),
        enforcedOptions: emptyMap(),
        tentativeOwner: 0n,
        endpointCode: args.endpointCode,
        channelCode: args.channelCode,
        endpointInitStorage: getEndpointInitStorage({
            controllerAddress: args.controllerAddress,
            path: {
                srcEid: args.srcEid,
                dstEid: 0,
            },
        }),
    })
}

export const buildPathClass = (pathway: PathWayLike): Cell => {
    return buildClass('lz::Path', {
        srcEid: pathway.srcEid,
        dstEid: pathway.dstEid,

        ...('receiver' in pathway
            ? {
                  srcOApp: pathway.sender,
                  dstOApp: pathway.receiver,
              }
            : {
                  srcOApp: pathway.srcOApp,
                  dstOApp: pathway.dstOApp,
              }),
    })
}

export const initWorkerCoreStorage = (admins: AddressListTypeLike, proxy: bigint, version: bigint): Cell => {
    return buildClass('WorkerCoreStorage', {
        admins,
        proxy,
        version,
    })
}

export const getEndpointInitStorage = ({
    controllerAddress,
    path,
}: {
    path: PathLike
    controllerAddress: AddressTypeLike
}): Cell => {
    return buildClass('Endpoint', {
        baseStorage: initBaseStorage(controllerAddress),
        eid: path.srcEid,
        dstEid: path.dstEid,
        msglibs: emptyMap(),
        numMsglibs: 0,
        channelCode: emptyCell(),
        channelStorageInit: nullObject(),
        defaultSendMsglibManager: 0n,
        defaultSendLibInfo: nullObject(),
        defaultReceiveLibInfo: nullObject(),
        defaultTimeoutReceiveLibInfo: nullObject(),
        defaultExpiry: 0,
    })
}
