import {
    Address,
    Cell,
    Dictionary,
    MessageRelaxed,
    beginCell,
    loadMessageRelaxed,
    storeMessageRelaxed,
} from '@ton/core'
import { toBigIntBE, toBufferBE } from 'bigint-buffer'

import { AddressTypeLike } from './class'

export const PUBLIC_KEY_BYTE_LENGTH = 64

export function publicKeyToHash(publicKey: Uint8Array): bigint {
    const publicKeyCell = beginCell().storeBuffer(Buffer.from(publicKey), PUBLIC_KEY_BYTE_LENGTH).endCell()
    const publicKeyHash = BigInt('0x' + publicKeyCell.hash().toString('hex'))
    return publicKeyHash
}

export function createVerifierDictSet(verifiers: Uint8Array[]): Cell {
    const verifierDict = Dictionary.empty(Dictionary.Keys.BigUint(256), Dictionary.Values.Cell())
    for (const verifier of verifiers) {
        verifierDict.set(publicKeyToHash(verifier), beginCell().endCell())
    }
    return beginCell().storeDictDirect(verifierDict, Dictionary.Keys.BigUint(256), Dictionary.Values.Cell()).endCell()
}

export const getBocStringFromMessage = (message: MessageRelaxed): string => {
    return beginCell().store(storeMessageRelaxed(message)).endCell().toBoc().toString('base64')
}

export const getMessageFromBocString = (bocString: string): MessageRelaxed => {
    const cell = Cell.fromBase64(bocString)
    const cellSlice = cell.beginParse()
    return loadMessageRelaxed(cellSlice)
}

export function bigintToAddress(value: bigint): Address {
    const buf = to32ByteBuffer(value)
    return Address.parse(`0:${buf.toString('hex')}`)
}

export const parseTonAddress = (address: AddressTypeLike): Address => {
    if (address instanceof Address) {
        return address
    }

    if (typeof address === 'bigint' || typeof address === 'number') {
        return bigintToAddress(BigInt(address))
    }

    if (address.startsWith('0x')) {
        // If it's a hex address format it to ton format
        return bigintToAddress(BigInt(address))
    }

    try {
        return Address.parse(address)
    } catch (e) {
        // handles the case where the address is hex format without leading 0x
        return bigintToAddress(BigInt(`0x${address}`))
    }
}

const _addressToNotPaddedHex = (address: AddressTypeLike): string => {
    return `0x${parseTonAddress(address).hash.toString('hex')}`
}

export const bigIntToHex = (bigInt: bigint): string => {
    return `0x${bigInt.toString(16)}`.toLowerCase()
}

export const addressToHex = (address: AddressTypeLike): string => {
    return `0x${to32ByteBuffer(_addressToNotPaddedHex(address)).toString('hex')}`.toLowerCase()
}

export const addressToBigInt = (address: AddressTypeLike): bigint => {
    return BigInt(_addressToNotPaddedHex(address))
}

export const compareAddresses = (addressA: AddressTypeLike, addressB: AddressTypeLike): boolean => {
    return parseTonAddress(addressA).equals(parseTonAddress(addressB))
}

/**
 * Convert a value to a 32 byte buffer, if the value is a string, it can only be a hex string
 * @param value
 * @param maxIntermediateBufferSize
 */
export function to32ByteBuffer(value: bigint | number | string | Uint8Array, maxIntermediateBufferSize = 66): Buffer {
    if (typeof value === 'string') {
        if (!isValidHex(value)) {
            throw new Error('only hex string is supported')
        }
        let hex = trim0x(value)
        if (hex.length % 2 !== 0) {
            hex = '0' + hex
        }
        value = toBigIntBE(Buffer.from(hex, 'hex'))
    }
    if (value instanceof Uint8Array) {
        value = toBigIntBE(Buffer.from(value))
    }
    const bf = toBufferBE(BigInt(value), maxIntermediateBufferSize)
    // trim from the left, keep the right 32 bytes
    return bf.subarray(-32)
}

function trim0x(str: string): string {
    return str.replace(/^0x/, '')
}

function isValidHex(str: string): boolean {
    const hexRegex = /^(0x)?[0-9A-Fa-f]+$/
    return hexRegex.test(str)
}
