/* eslint-disable @typescript-eslint/no-explicit-any */
// from https://github.com/LayerZero-Labs/offchain-monorepo/blob/main/packages/common-ton/src/class/buildClass.ts

import { Address, Cell, Dictionary, DictionaryKeyTypes, DictionaryValue, beginCell } from '@ton/core'

import { getCellName } from './helpers'
import {
    AddressListTypeLike,
    AddressTypeLike,
    BoolTypeLike,
    ClDeclareField,
    ClassTypeLike,
    DecodedAndRaw,
    DictionaryTypeLike,
    NumberTypeLike,
    _getTypeWidth,
    asciiStringToBigint,
    cl,
    clDeclare,
    clGetCellRef,
    clGetUint,
    deserializeAddressList,
    serializeAddressList,
} from './index'
import { cellsToHex } from '..'
import { bigIntToHex, bigintToAddress } from '../utils'

export type ValueOf<T> = T[keyof T]

export const fieldTypes = [
    'cl::t::uint1', // 1 bit
    'cl::t::bool', // 1 bit
    'cl::t::uint2', // 2 bits
    'cl::t::uint4', // 4 bits
    'cl::t::uint8', // 8 bits
    'cl::t::uint16', // 16 bits
    'cl::t::uint32', // 32 bits
    'cl::t::uint64', // 64 bits
    'cl::t::uint128', // 128 bits
    'cl::t::coins', // 128 bits
    'cl::t::uint256', // 256 bits
    'cl::t::address', // 256 bits
    'cl::t::dict256', // cell
    'cl::t::cellRef', // cell
    'cl::t::objRef', // cell
    'cl::t::addressList', // cell
] as const

export interface TonObjects {
    [key: string]: {
        name: string
    } & {
        [key in `${number}`]: {
            fieldName: string
            // eslint-disable-next-line no-use-before-define
            fieldType: FieldTypes<TonObjects>
        }
    }
}
export type TonObjectsKeys<_TonObjects extends TonObjects> = keyof _TonObjects & string

export type FieldTypes<_TonObjects extends TonObjects> = (typeof fieldTypes)[number] | TonObjectsKeys<_TonObjects>

export type ClassDefinition<_TonObjects extends TonObjects, ClassName extends TonObjectsKeys<_TonObjects>> = Omit<
    _TonObjects[ClassName],
    'name'
>

export type EnhancedAddress = Address & {
    toHex: () => string
    toBigInt: () => bigint
}
export type EnhancedAddressList = EnhancedAddress[] & {
    toHex: () => string[]
    toBigInt: () => bigint[]
}

export type EnhancedCell<_TonObjects extends TonObjects> = Cell & {
    getAddressList: () => EnhancedAddressList
    cellsToHex: () => string
    getDict: <V>(valueType: DictionaryValue<V>) => Dictionary<bigint, V>
    decode: <ClassName extends TonObjectsKeys<_TonObjects>>(
        className: ClassName
        // eslint-disable-next-line no-use-before-define
    ) => Fields<_TonObjects, ClassName>
    clGetCellRef: (index: number) => EnhancedCell<_TonObjects>
    clGetUint: (index: number, width: number) => bigint
    getCellName: () => string
    rawCell: Cell
}

export type LoadableDict<_TonObjects extends TonObjects> = DecodedAndRaw<
    {
        getDict: <V>(valueType: DictionaryValue<V>) => Dictionary<bigint, V>
    },
    EnhancedCell<_TonObjects>
>

export type FieldTypeToValue<
    _TonObjects extends TonObjects,
    FieldType,
    ClassType extends ClassTypeLike | EnhancedCell<_TonObjects>,
    NumberType extends NumberTypeLike,
    BoolType extends BoolTypeLike,
    AddressType extends AddressTypeLike,
    AddressListType extends AddressListTypeLike,
    DictionaryType extends DictionaryTypeLike<any>,
> =
    FieldType extends TonObjectsKeys<_TonObjects>
        ? // The following ternary is to decide between the type used on decode/buildClass
          [ClassTypeLike] extends [ClassType]
            ? // eslint-disable-next-line no-use-before-define
              Fields<_TonObjects, FieldType, ClassType, NumberType, BoolType, AddressType, AddressListType> | ClassType
            : DecodedAndRaw<
                  // eslint-disable-next-line no-use-before-define
                  Fields<_TonObjects, FieldType, ClassType, NumberType, BoolType, AddressType, AddressListType>,
                  EnhancedCell<_TonObjects>
              >
        : FieldType extends 'cl::t::cellRef' | 'cl::t::objRef'
          ? ClassType
          : FieldType extends 'cl::t::bool'
            ? BoolType
            : FieldType extends 'cl::t::address'
              ? AddressType
              : FieldType extends 'cl::t::addressList'
                ? AddressListType
                : FieldType extends 'cl::t::dict256'
                  ? DictionaryType
                  : NumberType

export type Fields<
    _TonObjects extends TonObjects,
    ClassName extends TonObjectsKeys<_TonObjects>,
    ClassType extends ClassTypeLike | EnhancedCell<_TonObjects> = EnhancedCell<_TonObjects>,
    NumberType extends NumberTypeLike = bigint,
    BoolType extends BoolTypeLike = boolean,
    AddressType extends AddressTypeLike = EnhancedAddress,
    AddressListType extends AddressListTypeLike = EnhancedAddressList,
    DictionaryType extends DictionaryTypeLike<any> = LoadableDict<_TonObjects>,
> =
    ValueOf<ClassDefinition<_TonObjects, ClassName>> extends {
        fieldName: string
        fieldType: FieldTypes<_TonObjects>
    }
        ? {
              [K in ValueOf<
                  ClassDefinition<_TonObjects, ClassName>
              > as K['fieldName'] extends `${ClassName}::${infer PropName}` ? PropName : never]: FieldTypeToValue<
                  _TonObjects,
                  K['fieldType'],
                  ClassType,
                  NumberType,
                  BoolType,
                  AddressType,
                  AddressListType,
                  DictionaryType
              >
          }
        : never

export const isClassName = <_TonObjects extends TonObjects>(
    _tonObjects: _TonObjects,
    fieldType: FieldTypes<_TonObjects>
): fieldType is TonObjectsKeys<_TonObjects> => {
    // eslint-disable-next-line @typescript-eslint/no-unnecessary-condition
    return _tonObjects[fieldType] !== undefined
}

const getClassNameAndDefinition =
    <_TonObjects extends TonObjects>(_tonObjects: _TonObjects) =>
    <ClassName extends TonObjectsKeys<_TonObjects>>(
        _className: ClassName
    ): {
        className: ClassName
        classDefinition: (typeof _tonObjects)[ClassName]
    } => {
        let className = _className
        let classDefinition = _tonObjects[className]

        // When deep decoding a class that you don't have the whole definition for
        // you can pass the name of the class definition instead of the class name.
        // This works because the first bits of the class are the name itself, so it
        // allows us to decode the class without the full definition.
        // eslint-disable-next-line @typescript-eslint/no-unnecessary-condition, @typescript-eslint/strict-boolean-expressions
        if (!classDefinition) {
            // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
            ;[className, classDefinition] = Object.entries(_tonObjects).find(
                ([_, value]) => value.name === className
            )! as [ClassName, (typeof _tonObjects)[ClassName]]
        }

        // eslint-disable-next-line @typescript-eslint/no-unnecessary-condition, @typescript-eslint/strict-boolean-expressions
        if (!classDefinition) {
            throw new Error(`Class ${className} not found`)
        }

        return { className, classDefinition }
    }

export const generateBuildClass =
    <_TonObjects extends TonObjects>(_tonObjects: _TonObjects) =>
    <ClassName extends TonObjectsKeys<_TonObjects>>(
        _className: ClassName,
        fields: Fields<
            _TonObjects,
            ClassName,
            ClassTypeLike,
            NumberTypeLike,
            BoolTypeLike,
            AddressTypeLike,
            AddressListTypeLike,
            DictionaryTypeLike<any>
        >
    ): Cell => {
        const { className, classDefinition } = getClassNameAndDefinition(_tonObjects)(_className)

        const fieldsArray = Object.entries(classDefinition).filter(([_, field]) => typeof field !== 'string') as [
            string,
            { fieldName: string; fieldType: FieldTypes<_TonObjects> },
        ][]

        const results = Object.entries(fields)
            .map(([fieldName, fieldValue]) => {
                // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
                const [index, field] = fieldsArray.find(
                    ([_, field]) => field.fieldName === `${className}::${fieldName}`
                )!

                if (field.fieldType === 'cl::t::dict256' && !(fieldValue instanceof Cell)) {
                    return [
                        Number(index),
                        cl.t.objRef,
                        // eslint-disable-next-line @typescript-eslint/no-unnecessary-condition
                        (fieldValue as LoadableDict<_TonObjects>).rawCell ??
                            beginCell()
                                .storeDict(fieldValue as Dictionary<DictionaryKeyTypes, unknown>)
                                .endCell()
                                .asSlice()
                                .loadRef(),
                    ] as const
                }

                if (field.fieldType === 'cl::t::addressList' && !(fieldValue instanceof Cell)) {
                    return [
                        Number(index),
                        cl.t.objRef,
                        serializeAddressList(fieldValue as AddressListTypeLike),
                    ] as const
                }

                if (isClassName(_tonObjects, field.fieldType) && !(fieldValue instanceof Cell)) {
                    return [
                        Number(index),
                        cl.t.objRef,
                        generateBuildClass(_tonObjects)(
                            field.fieldType,
                            fieldValue as Fields<
                                _TonObjects,
                                typeof field.fieldType,
                                ClassTypeLike,
                                NumberTypeLike,
                                BoolTypeLike,
                                AddressTypeLike,
                                AddressListTypeLike,
                                DictionaryTypeLike<any>
                            >
                        ),
                    ] as const
                }

                // eslint-disable-next-line @typescript-eslint/no-unnecessary-condition
                const tNumber = cl.t[field.fieldType.split('cl::t::')[1] as keyof typeof cl.t] ?? cl.t.objRef

                return [Number(index), tNumber, fieldValue] as const
            })
            .sort(([a], [b]) => a - b)
            .map(([_, type, value]) => ({ type, value })) as ClDeclareField[]

        return clDeclare(asciiStringToBigint(_tonObjects[className].name), results)
    }

export const generateDecodeClass =
    <_TonObjects extends TonObjects>(_tonObjects: _TonObjects) =>
    <ClassName extends TonObjectsKeys<_TonObjects>>(
        _className: ClassName,
        cell: Cell
    ): Fields<_TonObjects, ClassName> => {
        const { className, classDefinition } = getClassNameAndDefinition(_tonObjects)(_className)

        const enhanceCell = (cell: Cell): EnhancedCell<_TonObjects> => {
            return new Proxy(cell, {
                // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
                get(target, prop) {
                    if (prop === 'getAddressList') {
                        // eslint-disable-next-line no-use-before-define
                        return () => enhanceAddressList(cell)
                    }

                    if (prop === 'cellsToHex') {
                        return () => cellsToHex(cell)
                    }

                    if (prop === 'getDict') {
                        // eslint-disable-next-line no-use-before-define
                        return enhancedLoadDict(cell).getDict
                    }

                    if (prop === 'decode') {
                        // eslint-disable-next-line no-use-before-define
                        return <ClassName extends TonObjectsKeys<_TonObjects>>(className: ClassName) =>
                            generateDecodeClass(_tonObjects)(className, cell)
                    }

                    if (prop === 'clGetCellRef') {
                        return (index: number) => enhanceCell(clGetCellRef(cell, index))
                    }

                    if (prop === 'clGetUint') {
                        return (index: number, width: number) => clGetUint(cell, index, width)
                    }

                    if (prop === 'getCellName') {
                        return () => getCellName(cell)
                    }

                    if (prop === 'rawCell') {
                        return cell
                    }

                    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
                    return Reflect.get(target, prop)
                },
            }) as EnhancedCell<_TonObjects>
        }

        const injectRawCell = <V extends object>(
            value: V,
            rawCell: Cell
        ): DecodedAndRaw<V, EnhancedCell<_TonObjects>> => {
            return new Proxy(value, {
                // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
                get(target, prop) {
                    if (prop === 'rawCell') {
                        return enhanceCell(rawCell)
                    }

                    return Reflect.get(target, prop)
                },
            }) as DecodedAndRaw<V, EnhancedCell<_TonObjects>>
        }

        const enhanceAddress = (address: bigint): EnhancedAddress => {
            return new Proxy(bigintToAddress(address), {
                // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
                get(target, prop) {
                    if (prop === 'toHex') {
                        return () => bigIntToHex(address)
                    }

                    if (prop === 'toBigInt') {
                        return () => address
                    }

                    // eslint-disable-next-line @typescript-eslint/no-unsafe-return
                    return Reflect.get(target, prop)
                },
            }) as EnhancedAddress
        }

        const enhanceAddressList = (rawCell: Cell): DecodedAndRaw<EnhancedAddressList, EnhancedCell<_TonObjects>> => {
            const addressList = deserializeAddressList(rawCell).map(enhanceAddress)
            return injectRawCell(
                new Proxy(addressList, {
                    // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
                    get(target, prop) {
                        if (prop === 'toHex') {
                            return () => addressList.map((address) => address.toHex())
                        }

                        if (prop === 'toBigInt') {
                            return () => addressList.map((address) => address.toBigInt())
                        }

                        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
                        return Reflect.get(target, prop)
                    },
                }) as EnhancedAddressList,
                rawCell
            )
        }

        const enhancedLoadDict = (rawCell: Cell): LoadableDict<_TonObjects> => {
            return {
                // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
                getDict: (value) => {
                    return rawCell.bits.length === 0
                        ? Dictionary.empty(Dictionary.Keys.BigUint(256), value)
                        : rawCell.beginParse().loadDictDirect(Dictionary.Keys.BigUint(256), value)
                },
                rawCell: enhanceCell(rawCell),
            }
        }

        // eslint-disable-next-line @typescript-eslint/no-unsafe-return
        return Object.fromEntries(
            (
                Object.entries(classDefinition) as [
                    string,
                    {
                        fieldName: string
                        fieldType: FieldTypes<_TonObjects>
                    },
                ][]
            )
                .filter(([_, field]) => typeof field !== 'string')
                .map(([index, field]) => {
                    const tNumber = cl.t[field.fieldType.split('cl::t::')[1] as keyof typeof cl.t]

                    const fieldName = field.fieldName.split(`${className}::`)[1]

                    // eslint-disable-next-line @typescript-eslint/strict-boolean-expressions, @typescript-eslint/no-unnecessary-condition
                    if (_tonObjects[field.fieldType]) {
                        const rawCell = clGetCellRef(cell, Number(index))

                        return [
                            fieldName,
                            new Proxy(
                                generateDecodeClass(_tonObjects)(field.fieldType, clGetCellRef(cell, Number(index))),
                                {
                                    // eslint-disable-next-line @typescript-eslint/explicit-function-return-type
                                    get(target, prop) {
                                        if (prop === 'rawCell') {
                                            return enhanceCell(rawCell)
                                        }

                                        return Reflect.get(target, prop)
                                    },
                                }
                            ),
                        ]
                    }

                    if (field.fieldType === 'cl::t::addressList') {
                        const rawCell = clGetCellRef(cell, Number(index))

                        return [fieldName, enhanceAddressList(rawCell)]
                    }

                    if (field.fieldType === 'cl::t::dict256') {
                        return [fieldName, enhancedLoadDict(clGetCellRef(cell, Number(index)))]
                    }

                    if (tNumber === cl.t.objRef) {
                        return [fieldName, enhanceCell(clGetCellRef(cell, Number(index)))]
                    }

                    if (field.fieldType === 'cl::t::address') {
                        return [fieldName, enhanceAddress(clGetUint(cell, Number(index), _getTypeWidth(tNumber)))]
                    }

                    if (field.fieldType === 'cl::t::bool') {
                        return [fieldName, clGetUint(cell, Number(index), _getTypeWidth(tNumber)) === 1n]
                    }

                    return [fieldName, clGetUint(cell, Number(index), _getTypeWidth(tNumber))]
                })
        )
    }

const baseObjects = {
    POOO: {
        '0': {
            fieldName: 'POOO::nextEmpty',
            fieldType: 'cl::t::uint64',
        },
        '1': {
            fieldName: 'POOO::bitmap',
            fieldType: 'cl::t::cellRef',
        },
        name: 'POOO',
    },
    BaseStorage: {
        '0': {
            fieldName: 'BaseStorage::owner',
            fieldType: 'cl::t::address',
        },
        '1': {
            fieldName: 'BaseStorage::authenticated',
            fieldType: 'cl::t::bool',
        },
        '2': {
            fieldName: 'BaseStorage::initialized',
            fieldType: 'cl::t::bool',
        },
        '3': {
            fieldName: 'BaseStorage::initialStorage',
            fieldType: 'cl::t::cellRef',
        },
        name: 'baseStore',
    },
    'action::event': {
        '0': {
            fieldName: 'action::event::topic',
            fieldType: 'cl::t::uint256',
        },
        '1': {
            fieldName: 'action::event::body',
            fieldType: 'cl::t::objRef',
        },
        '2': {
            fieldName: 'action::event::initialStorage',
            fieldType: 'cl::t::objRef',
        },
        name: 'event',
    },
} as const

export const baseBuildClass = generateBuildClass(baseObjects)
export const baseDecodeClass = generateDecodeClass(baseObjects)
