/* eslint-disable @typescript-eslint/no-non-null-assertion */
// https://github.com/LayerZero-Labs/offchain-monorepo/blob/main/packages/common-ton/src/class/index.ts
import { Address, BitString, Cell, Dictionary, beginCell } from '@ton/core'

import { emptyCell } from './helpers'
import { addressToBigInt } from '../utils'

export * from './buildClass'
export * from './helpers'

export const cl = {
    t: {
        uint1: 0,
        bool: 0, // same as uint1
        uint2: 1,
        uint4: 2,
        uint8: 3,
        uint16: 4,
        uint32: 5,
        uint64: 6,
        uint128: 7,
        coins: 7, // same as uint128
        uint256: 8,
        address: 8, // same as uint256
        cellRef: 9, // same as cellRef
        dict256: 9, // same as cellRef
        objRef: 9, // same as cellRef
        addressList: 9, // same as cellRef
    },
    NULL_CLASS_NAME: 'NULL',
    ERROR: {
        INVALID_CLASS: 'INVALID_CLASS',
    },
} as const

const MAX_NAME_LEN = 10
export const NAME_WIDTH = 8 * MAX_NAME_LEN

const FIELD_TYPE_WIDTH = 4
const CELL_ID_WIDTH = 2
const DATA_OFFSET_WIDTH = 10
const REF_OFFSET_WIDTH = 2
const FIELD_INFO_WIDTH = FIELD_TYPE_WIDTH + CELL_ID_WIDTH + DATA_OFFSET_WIDTH + REF_OFFSET_WIDTH
const MAX_CLASS_FIELDS = 15

const BASIC_HEADER_WIDTH = NAME_WIDTH
const HEADER_WIDTH = BASIC_HEADER_WIDTH + MAX_CLASS_FIELDS * FIELD_INFO_WIDTH
export const MAX_CELL_BITS = 1023
const MAX_CELL_REFS = 4

export type ClassTypeLike = Cell
export type NumberTypeLike = string | bigint | number
export type BoolTypeLike = NumberTypeLike | boolean
export type AddressTypeLike = Address | string | bigint
export type AddressListTypeLike = AddressTypeLike[] | Cell
export type DecodedAndRaw<T, _Cell extends Cell = Cell> = T & {
    rawCell: _Cell
}
// eslint-disable-next-line @typescript-eslint/ban-types
export type DictionaryTypeLike<V> = Dictionary<bigint, V> | Cell | DecodedAndRaw<{}>

export interface ClDeclareCellField {
    type: typeof cl.t.objRef
    value: Cell
}

export interface ClDeclareNumericField {
    type: (typeof cl.t)[keyof Omit<typeof cl.t, 'objRef' | 'cellRef' | 'dict256'>]
    value: NumberTypeLike | BoolTypeLike | AddressTypeLike
}

export type ClDeclareField = ClDeclareCellField | ClDeclareNumericField

/**
 * Throws an error if value is smaller than -1
 */
export const numberTypeLikeToAbsBigInt = (val: NumberTypeLike | BoolTypeLike | AddressTypeLike): bigint => {
    if (val instanceof Address) {
        return addressToBigInt(val)
    }

    if (typeof val === 'string' && (Address.isFriendly(val) || Address.isRaw(val))) {
        return addressToBigInt(Address.parse(val))
    }

    const _val = BigInt(val)

    if (_val < -1n) {
        throw new Error('Invalid value. Only accepted negative value is -1')
    }

    return _val < 0 ? -_val : _val
}

export const _getTypeWidth = (clType: (typeof cl.t)[keyof typeof cl.t]): number => {
    if (clType <= cl.t.uint256) {
        return 1 << clType
    }

    return 0
}

const isNumericType = (field: ClDeclareField): field is ClDeclareNumericField => {
    // On TON classes all the fields are stored as a Cell or a uint and
    // and the types are ordered in a way that all the uints are lower than
    // the class values
    return field.type <= cl.t.uint256
}

const isCellType = (field: ClDeclareField): field is ClDeclareCellField => {
    // For all the cells, the type is always the same number stored as cellRef
    // and all the other types that are cells map to the same number
    return field.type == cl.t.cellRef
}

export const clDeclare = (name: bigint, fields: ClDeclareField[]): Cell => {
    if (fields.length > MAX_CLASS_FIELDS) {
        throw new Error(cl.ERROR.INVALID_CLASS)
    }

    const classBuilder = [null, beginCell()]

    let headerBuilder = beginCell().storeUint(name, NAME_WIDTH)

    let curDataCell = 1
    let curRefCell = 1
    let curCellMaxRefs = 2
    let curDataOffset = HEADER_WIDTH
    let curRefOffset = 0

    for (const field of fields) {
        const fieldBits = _getTypeWidth(field.type)

        if (curDataOffset + fieldBits > MAX_CELL_BITS) {
            curDataCell += 1
            curDataOffset = 0
            if (curDataCell >= classBuilder.length) {
                classBuilder.push(beginCell())
            }
        }
        if (fieldBits == 0) {
            if (curRefOffset + 1 > curCellMaxRefs) {
                curRefCell += 1
                curRefOffset = 0
                curCellMaxRefs = MAX_CELL_REFS
                if (curRefCell >= classBuilder.length) {
                    classBuilder.push(beginCell())
                }
            }
        }

        if (isNumericType(field)) {
            classBuilder[curDataCell] = classBuilder[curDataCell]!.storeUint(
                numberTypeLikeToAbsBigInt(field.value),
                fieldBits
            )
        } else if (isCellType(field)) {
            classBuilder[curRefCell] = classBuilder[curRefCell]!.storeRef(field.value)
        } else {
            throw new Error(cl.ERROR.INVALID_CLASS)
        }

        headerBuilder = headerBuilder.storeUint(field.type, FIELD_TYPE_WIDTH)
        if (fieldBits > 0) {
            headerBuilder = headerBuilder
                .storeUint(curDataCell == 1 ? 0 : curDataCell, CELL_ID_WIDTH)
                .storeUint(curDataOffset, DATA_OFFSET_WIDTH)
                .storeUint(3, REF_OFFSET_WIDTH)
            curDataOffset += fieldBits
        } else {
            headerBuilder = headerBuilder
                .storeUint(curRefCell == 1 ? 0 : curRefCell, CELL_ID_WIDTH)
                .storeUint(MAX_CELL_BITS, DATA_OFFSET_WIDTH)
                .storeUint(curRefOffset, REF_OFFSET_WIDTH)
            curRefOffset += 1
        }
    }

    let rootBuilder = classBuilder[1]!
    const numCells = classBuilder.length - 1

    if (numCells > 1) {
        if (rootBuilder.refs == 0) {
            rootBuilder = rootBuilder.storeRef(emptyCell()).storeRef(emptyCell())
        } else if (rootBuilder.refs == 1) {
            rootBuilder = rootBuilder.storeRef(emptyCell())
        }
    }

    const trailingOnes = HEADER_WIDTH - headerBuilder.bits

    headerBuilder = headerBuilder
        .storeBits(new BitString(Buffer.from('F'.repeat(trailingOnes), 'hex'), 0, trailingOnes))
        .storeBuilder(rootBuilder)

    if (numCells == 1) {
        return headerBuilder.endCell()
    }

    if (numCells == 2) {
        return headerBuilder.storeRef(classBuilder[2]!.endCell()).endCell()
    }

    return headerBuilder.storeRef(classBuilder[2]!.endCell()).storeRef(classBuilder[3]!.endCell()).endCell()
}

const loadFieldInfo = (
    cell: Cell,
    fieldName: number
): { fieldCellIndex: number; fieldOffset: number; fieldRefIdx: number } => {
    const headerSlice = cell.beginParse()
    const fieldInfoOffset = BASIC_HEADER_WIDTH + fieldName * FIELD_INFO_WIDTH
    headerSlice.skip(fieldInfoOffset + FIELD_TYPE_WIDTH)
    const fieldCellIndex = headerSlice.loadUint(CELL_ID_WIDTH)
    const fieldOffset = headerSlice.loadUint(DATA_OFFSET_WIDTH)
    const fieldRefIdx = headerSlice.loadUint(REF_OFFSET_WIDTH)
    return { fieldCellIndex, fieldOffset, fieldRefIdx }
}

export const clGetUint = (cell: Cell, fieldName: number, width: number): bigint => {
    const headerSlice = cell.beginParse()
    const { fieldCellIndex, fieldOffset } = loadFieldInfo(cell, fieldName)

    if (fieldCellIndex == 0) {
        return headerSlice.skip(fieldOffset).loadUintBig(width)
    }

    for (let i = 0; i < fieldCellIndex; i++) {
        headerSlice.loadRef()
    }

    return headerSlice.loadRef().beginParse().skip(fieldOffset).loadUintBig(width)
}

export const clGetCellRef = (cell: Cell, fieldName: number): Cell => {
    const headerSlice = cell.beginParse()
    const { fieldCellIndex, fieldRefIdx } = loadFieldInfo(cell, fieldName)

    if (fieldCellIndex == 0) {
        for (let i = 0; i < fieldRefIdx; i++) {
            headerSlice.loadRef()
        }

        return headerSlice.loadRef()
    }

    for (let i = 0; i < fieldCellIndex; i++) {
        headerSlice.loadRef()
    }

    const cellRef = headerSlice.loadRef().beginParse()

    for (let i = 0; i < fieldRefIdx; i++) {
        cellRef.loadRef()
    }

    return cellRef.loadRef()
}
